import React from "react";


function Todo({ text, todo, todos, setTodos, filter, setFilter }) {



  const deleteHandler = () => {
    setTodos(todos.filter((el) => el.id !== todo.id));
  };

  const completedHandler = () => {
    setTodos(
      todos.map((item) => {
        if (item.id === todo.id) {
          return { ...item, completed: !item.completed };
        }
        return item;
      })
    );
  };




const filtering = () => {
 const test=  todos.filter((el)=>el.completed === false)
setTodos(test)

}

  return (
    <div className="todo">
      <li className={`todo-item ${todo.completed ? "completed" : null}`}>
        {text}
      </li>

      {/* <form onsubmit={editSubmit} className={`${todo.editing ? "show" : "hidden"}`} >
        <input className="todo-item" type="text" value={text} />
      </form> */}

      <button onClick={completedHandler} className="complete-btn">
        <i className="fas fa-check"></i>
      </button>

      <button onClick={deleteHandler} className="trash-btn">
        <i className="fas fa-trash"></i>
      </button>
    </div>
  );
}

export default Todo;
