import React from "react";


function TodoForm({ inputText, setInputText, todos, setTodos, filterStatus, setFilterStatus }) {
  
  const inputTextHandler = (e,) => {
    setInputText(e.target.value);
    
  };

  const submitTodoHandler = (e) => {
    e.preventDefault();
    setTodos([
      ...todos,
      {
        text: inputText,
        completed: false,
        id: Math.floor(Math.random() * 1000),
        editing: false,
      }
    ]);
    setInputText("");
  };

  const FilterStatusHandler = (e) => {
    console.log(e.target.value);
    setFilterStatus(e.target.value);
  }

 

  return (
    <form>
      <input
        value={inputText}
        onChange={inputTextHandler}
        type="text"
        className="todo-input"
        placeholder="write what you need to do"
      />
      <button onClick={submitTodoHandler} className="todo-button" type="submit">
        <i className="fas fa-plus-square"></i>
      </button>
      <div className="select">
        <select onChange={FilterStatusHandler} name="todos" className="filter-todo">
          <option value="all">All</option>
          <option value="completed">completed</option>
          <option value="uncompleted">Uncompleted</option>
        </select>
      </div>
    </form>
  );
}

export default TodoForm;
