import React, {useState, useEffect} from "react";
import '../node_modules/bootstrap/dist/css/bootstrap.min.css'
import "./App.css";
import TodoForm from "./components/TodoForm";
import TodoList from "./components/TodoList";


function App() {

  const [inputText, setInputText] = useState("")
  const [todos, setTodos] = useState([])
  const [filterStatus, setFilterStatus] = useState("all")
  const [filteredTodos,setFilteredTodos] = useState([])


useEffect(()=> {
  getLocalTodos();
}, [])  
  useEffect(()=> {
    filterHandler();
    saveLocalTodos();
  }, [todos, filterStatus]);

  const filterHandler = () => {
    switch (filterStatus) {
      case "completed":
        setFilteredTodos(todos.filter(todo => todo.completed === true))
        break;
      case "uncompleted":
        setFilteredTodos(todos.filter(todo => todo.completed === false))
      break;
      default: setFilteredTodos(todos)
        break;
    }
  }

  // save to localStorage
  const saveLocalTodos = ()=> {
    localStorage.setItem("todos", JSON.stringify(todos))
  }
  
  const getLocalTodos = ()=> {
    if (localStorage.getItem("todos") === null){
      localStorage.setItem("todos", JSON.stringify([]))
    } else {
     let todolocal= JSON.parse(localStorage.getItem("todos"))
     setTodos(todolocal)
    }
  }

  return (
    <div className="App mt-5 container bg-info">
      <header>
        <h1>Todo List</h1>
      </header>
      <TodoForm todos={todos} setTodos={setTodos} inputText={inputText} setInputText={setInputText} filterStatus={filterStatus} setFilterStatus={setFilterStatus}/>
      <TodoList todos={todos} setTodos={setTodos} filterStatus={filterStatus} setFilterStatus={setFilterStatus} filteredTodos={filteredTodos}/>
    </div>
  );
}

export default App;
